@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap"); /** Media Queries **/
body {
  font-family: "Poppins", sans-serif;
  background-color: #2c3e50;
}
body .navbar {
  width: 100%;
  margin: 0;
}

.menu {
  background-color: #2c3e50;
}

.offcanvas {
  background-color: #2c3e50;
}

.btn-orange {
  background-color: #ff9100;
  color: white;
}
.btn-orange:hover {
  background-color: #cc7400;
  color: #e6e6e6;
}

.dropdown-menu {
  background-color: #1f2c38;
}

.dropdown-item:hover {
  background-color: #1a252f;
}

.line-nav {
  color: white;
}

#loading {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #ff9100;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.formLogin {
  width: 90%;
  margin: auto;
}
@media (min-width: 768px) {
  .formLogin {
    width: 60%;
  }
}
@media (min-width: 1024px) {
  .formLogin {
    width: 30rem;
  }
}
.formLogin input {
  border-radius: none;
}

.lista-index .list-group-item {
  border: none;
  transition: 0.2s;
}
.lista-index .list-group-item:hover {
  background-color: #3e5871;
  border: none;
  color: white;
}

.asistencia-confirma input[type=checkbox] {
  transform: scale(2); /* Escalar el tamaÃ±o del checkbox */
  margin-right: 10px; /* AÃ±adir margen a la derecha */
  cursor: pointer;
}

#example {
  margin-bottom: 2rem;
}

.table-asistencia {
  background-color: #3e5871;
  color: white;
  border-radius: 6px;
}

.dataTables_length label select {
  color: white;
}
.dataTables_length label select:focus {
  background-color: #3e5871;
  color: white;
}

.dataTables_filter label input {
  color: white;
  background-color: #3e5871;
  border: 1px solid white;
  border-radius: 6px;
  padding: 6px;
  margin-left: 10px;
}
.dataTables_filter label input:focus {
  background-color: #3e5871;
  color: white;
}

.dataTables_wrapper .dataTables_paginate .paginate_button.disabled, .dataTables_wrapper .dataTables_paginate .paginate_button.disabled:hover, .dataTables_wrapper .dataTables_paginate .paginate_button.disabled:active {
  background-color: white;
  border-radius: 10rem;
  margin-left: 1rem;
  margin-right: 1rem;
  cursor: pointer;
  color: white;
}

.detalles {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 60%;
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .detalles {
    width: 60%;
  }
}
.detalles textarea {
  font-size: 0.8rem;
  width: 100%;
  height: 10rem;
  border: 1px solid #3e5871;
  border-radius: 6px;
  padding: 6px;
  margin-top: 10px;
  margin-bottom: 10px;
  outline: none;
}
@media (min-width: 768px) {
  .detalles textarea {
    width: 80%;
  }
}

.formAsistencias {
  display: flex;
  justify-content: center;
  margin: auto;
}
@media (min-width: 1024px) {
  .formAsistencias {
    max-width: 70%;
  }
}

.btnCursos {
  width: 100%;
  margin-top: 0;
}

#tablaAsistencias {
  margin-bottom: 2rem;
}

.dataTables_length {
  margin-bottom: 2rem;
}

.puertas .puertas-cont .puerta button {
  border-radius: 6px 6px 0 0;
}
.puertas .puertas-cont .puerta .historial {
  border-radius: 1rem;
}
.puertas .puertas-cont .puerta .historial li {
  list-style-type: none;
  border-bottom: 1px solid gray;
}
.puertas .puertas-cont .puerta .historial li:last-child {
  border-bottom: none;
  border-radius: 0 0 6px 6px;
}
.puertas .puertas-cont .puerta .historial li::before {
  content: "ðŸ•‘";
  color: green;
  font-size: 20px;
}

.btn-puertas {
  background-color: #f73939;
  color: white;
  font-weight: 600;
  transition: 0.3s;
}
.btn-puertas:hover {
  background-color: #f96a6a;
  color: white;
  list-style-type: none;
}